.circle {
    background-color: blue;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    animation: circleAnimation 8s infinite;
    box-shadow: 0 0 20px blue;
  }
  
  @keyframes circleAnimation {
    0% {
      transform: scale(1);
    }
    50% {
      transform: scale(3.6);
    }
    100% {
      transform: scale(1);
    }
  }
  .text {
    position: fixed;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    font-size: 24px;
  }
  
  .event-time {
    position: fixed;
    bottom: 40px; /* Adjust the positioning as needed */
    left: 50%;
    transform: translateX(-50%);
    font-size: 16px;
  }
  